import { events } from "@react-three/fiber";
import React from "react";
import {
  Button,
  Col,
  Container,
  FormCheck,
  Image,
  Row,
  Spinner,
  Stack,
} from "react-bootstrap";
import * as THREE from "three";

const PanelJuego = ({
  sendAction,
  setActivateDefaultView,
  partida,
  idJugador,
}) => {
  function handleQuitGame() {
    sendAction("quit", "Deseo salir");
    console.log("saliendo del juego");
  }
  function handleDefaultView() {
    setActivateDefaultView((prev) => {
      return !prev;
    });
  }
  function handleFinishTurn() {
    sendAction("finishTurn");
  }
  function handleHardRoll(e) {
    if (idJugador == partida.jugadorActual && partida.estado == "J") {
      const val = parseInt(e.target.innerText);
      sendAction("rollDice", val);
    }
  }
  const dValores = [0, 1, 2, 3, 4, 5, 6, 7];
  return (
    <Container className="px-0" style={{ height: "100%" }}>
      <Row style={{ height: "5%" }} />
      <Row style={{ height: "75%" }}>
        <Col />
        <Col className="col-2">
          {partida.estado == "C" &&
            partida.d1Ix != undefined &&
            partida.d2Ix != undefined && (
              <Row style={{ height: "60px" }}>
                <Image
                  className="px-0"
                  src={`/imagenes/dado1_${partida.d1Ix}.gif`}
                  style={{
                    position: "fixed",
                    display: "block",
                    width: "60px",
                  }}
                />
                <Image
                  className="px-0"
                  src={`/imagenes/dado2_${partida.d2Ix}.gif`}
                  style={{
                    position: "fixed",
                    display: "block",
                    width: "60px",
                  }}
                />
              </Row>
            )}
          <Row>
            {import.meta.env.DEV &&
              dValores.map((v) => {
                return (
                  <Button
                    key={v}
                    style={{
                      width: "30px",
                      height: "30px",
                      pointerEvents: "all",
                    }}
                    onClick={handleHardRoll}
                  >
                    {v}
                  </Button>
                );
              })}
          </Row>
        </Col>
      </Row>
      <Row style={{ height: "20%", pointerEvents: "all" }}>
        <Col></Col>
        {partida.estado == "Z" && idJugador == partida.jugadorActual && (
          <Col>
            <Image
              src="/imagenes/finishTurn.png"
              title="Terminar"
              onClick={handleFinishTurn}
              style={{
                cursor: "pointer",
                padding: 0,
                width: "32px",
                height: "32px",
                filter: "drop-shadow(2px 2px 1px rgba(0, 0, 0, 1))",
              }}
            />
          </Col>
        )}
        <Col>
          <Image
            src="/imagenes/defaultview.png"
            title="Centrar"
            onClick={handleDefaultView}
            style={{
              cursor: "pointer",
              padding: 0,
              width: "32px",
              height: "32px",
              filter: "drop-shadow(2px 2px 1px rgba(0, 0, 0, 1))",
            }}
          />
        </Col>
        <Col>
          <Image
            src="/imagenes/quitgame.png"
            title="Dejar partida"
            onClick={handleQuitGame}
            style={{
              cursor: "pointer",
              padding: 0,
              width: "32px",
              height: "32px",
              filter: "drop-shadow(2px 2px 1px rgba(0, 0, 0, 1))",
            }}
          />
        </Col>
        <Col></Col>
      </Row>
    </Container>
  );
};

export default PanelJuego;
